import React, { useState } from 'react';
import MovieCard from './MovieCard.jsx';

export default function SearchMovies(){

    const [query, setQuery] = useState('');

    const [movies, setMovies] = useState([])

    const searchMovies = async (e) => {
        e.preventDefault();
        console.log("Submitting...");

        const APIurl = `https://api.themoviedb.org/3/search/movie?api_key=b8c87a1c8ddc26238f50b8211ef3c29c&language=en-US&query=${query}&page=1&include_adult=false`;

        try {
            const res = await fetch(APIurl);
        const data = await res.json();
        console.log(data.results);
        setMovies(data.results);
        }catch(err){
            console.error(err);
        }
    };

    return (
        <div>
            <form className="form" onSubmit={searchMovies}>
                <label className="label" htmlFor="query">Movie Name</label>
                <input className="input" type="text" name="query" placeholder="Example: Stars Wars"
                    value={query} onChange={(e) => setQuery(e.target.value)}
                />
                <button className="button" type="submit">Search</button>
            </form>
            <div className="card-list">
                {movies.filter(movie => movie.poster_path).map(movie => (
                    <MovieCard movie={movie}  key={movie.id}/>
                ))}
            </div>
        </div>
    );
}