import React, { useState } from 'react';

export default function MovieCard(props) {

    const movie = props.movie;

    return (
        <div className="card">
            <img className="card--image" src={`https://image.tmdb.org/t/p/w185_and_h278_bestv2/${movie.poster_path}`}
            alt={props.movie.title + ' Poster'}
            />
            <div className="card--content">
                <h3 className="card--title">{props.movie.title}</h3>
                <p><small>RESLEASE DATE: {props.movie.release_date}</small></p>
                <p><small>RATING: {props.movie.vote_average}</small></p>
                <p className="card--desc">{props.movie.overview}</p>
            </div>
        </div>
    );
}